.nav_parent {
  display: flex;
  justify-content: space-evenly;
}
.nav_links {
  font-size: 1.2rem;
  text-align: center;
  padding: 5px;
  color: #28a4a4;
  border: 1px solid #28a4a4;
  border-radius: 5px;
  text-decoration: none;
  width: 100px;
}

.create_total_form {
  margin: 20px auto;
}

.create_total_parent {
  display: flex;
  flex-direction: column;
  margin: 10px auto;
  width: 60%;
}

.create_total_label {
  font-size: 1.5rem;
  margin: 0px auto 10px auto;
}

.create_total_input {
  width: 50%;
  font-size: 1.2rem;
  height: 30px;
  padding: 5px;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
}

.create_total_button {
  font-size: 1.2rem;
  width: 40%;
  border-radius: 5px;
  border: 1px solid #28a4a4;
  color: #28a4a4;
  height: 35px;
  margin: 10px auto;
}

.main_card {
  font-size: 1.2rem;
  background-color: white;
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  width: 43%;
  height: 65px;
  margin: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.month_main_card {
  font-size: 1.2rem;
  background-color: white;
  border: 1px solid #d9d9d9;
  border-radius: 10px;
  width: 48%;
  height: 65px;
  margin: 2px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.x_delete {
